<template>
  <div class="Layout">
    <!-- 子路由出口 -->
    <keep-alive>
      <!-- 使用keep-alive，保持数据的状态，即这个组件内部的子组件的状态都会被保存，但是有个前提，就是这个layout父组件本身不能被重新渲染 -->
      <!-- 因为这个父组件是没有被keep-alive保持状态的，当这个父组件发生路由跳转时，这个组件会被销毁，下次再跳转进这个父组件就要重新初始化渲染了 -->
      <!-- 子组件之间路由跳转，只要不该变父组件路由，就可以保存状态，但是一旦发生了父组件级别的路由跳转，导致父组件重新渲染，那么子组件的状态也会初始化 -->
      <router-view />
    </keep-alive>
    <van-tabbar v-model="active" route>
      <van-tabbar-item name="home" icon="home-o" to="/">首页</van-tabbar-item>
      <van-tabbar-item name="search" icon="search" to="/search">搜索</van-tabbar-item>
      <van-tabbar-item name="friends" icon="video-o" to="/video">视频</van-tabbar-item>
      <van-tabbar-item name="setting" icon="user-circle-o" to="/my">{{$store.state.token ? '我的' : '未登录' }}</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      active: "home",
    };
  },
  computed: {},
  watch: {},
  created() {},
  methods: {},
};
</script>
<style lang='less' scoped>
</style>